import React, { Component } from 'react';
import './App.css';

class App extends Component {
	renderDisk = (c, i, j) => {
		if(c === 1) {
			 return (<div className="disk" style={{backgroundColor: "white"}} id={i + '-' + j}></div>);
		} else if(c === -1) {
			return (<div className="disk" style={{backgroundColor: "black"}} id={i + '-' + j}></div>);
		} else {
			return (<div className="disk" style={{display: "none"}} id={i + '-' + j}></div>);
		}
	}
	renderSquare = (i, j) => {
		var cls = "row";
		if(this.props.last !== null && i === this.props.last.x && j === this.props.last.y) cls += " last-move";
	  return (
		<div className={cls} key={'row' + i + '-' + j} onClick={this.props.handleClick(i, j)}>
		  {this.renderDisk(this.props.board[i][j], i, j)}
		</div>
	  );
  }
  renderCol(i) {
	  var list = [];
	  var j;
		for(j=0;j<8;j++) list.push(this.renderSquare(i, j));
	  return (
		<div  key={'col' + i} className="col">
		  {list}
		</div>
	  );
  }
  render() {
	  var list = [];
	  var i;
	  for(i=0;i<8;i++) list.push(this.renderCol(i));
    return (
      <div className="App">
		{list}
		<input type="button" value="reset" onClick={this.props.reset} />
		<div id="info">{this.props.info}</div>
      </div>
    );
  }
}

export default App;



